.header {
     width: 100%;
     background-color: #4371b1 ;
     color: white;
     display: flex;
     justify-content: center;

     position: relative;
}

.headerContainer {
     width: 100%;
     max-width: 1024px;
     margin: 20px 0px 100px 0px;
   }

.headerContainer.listMode {
     margin: 20px 0px 0px 0px;
}


.headerList{
     display: flex;
     gap: 40px;
     margin-bottom: 50px; 
     flex-flow: wrap;
}

.headerListItem{
     display: flex;
     align-items: center;
     gap: 10px;
}

.headerListItem.active{
     border: 1px solid white;
     padding: 10px;
     border-radius: 20px;
     
}

.headerDesc {
     margin: 20px 0px;

}
.headerBtn {
background-color: #0071c2;
color: white;
font-weight: 500;
border: none;
padding: 10px;
cursor: pointer;
}
 
.headerSearch {
     height: 30px;
     background-color: white;
     border: 3px solid #febb02;
     display: flex; 
     align-items: center;
     justify-content: space-around;
     padding: 10px 0px;
     border-radius: 5px;
     position: absolute;
     bottom: -25px;
     width: 100%;
     max-width: 1024px;

}

.headerIcon {
     color: lightgray;
}



.headerSearchItem {
   display: flex;
   align-items: center;
   gap: 10px;

}

.headerSearchItem .toolTipText {
     visibility: hidden;
     opacity: 0;
     width: 220px;
     background-color: rgb(255, 255, 255);
     color: rgb(0, 0, 0);
     text-align: center;
     border-radius: 6px;
     padding: 5px 0;
   
     /* Position the tooltip */
     position: absolute;
     top: -120%;
     transition:  visibility 3s, opacity 3s ease;

     z-index: 1;
   }
.headerSearchItem:hover .toolTipText {
     visibility: visible;
   
     opacity: 1;
    

}


.headerSearchInput {
     border: none;
     outline: none;
     
}

.headerSearchText {
     color: lightgray;
     cursor: pointer;

}

.date {
     position: absolute;
     top: 50px;
     z-index: 2;
}

.options {
     z-index: 2;
     position: absolute;
     top: 50px;
     background-color: white;
     color: gray;
     border-radius: 5px ;
     -webkit-box-shadow: 0px 0px 10px -5px rgba(0,0,0,0.4);
     box-shadow: 0px 0px 10px -5px rgba(0, 0, 0, 0.4);
}

.optionItem {
     width: 200px;
     display: flex;
     justify-content: space-between;
     margin: 10px;
     align-items: center;

}

.optionCounter {
     display: flex;
     align-items: center;
     gap: 10px;
     font-size: 12px;
     color: black;
}

.optionCounterButton {
     width: 30px;
     height: 30px;
     border-radius: 11%;
     border: 1px solid #0071c2;
     color: #0071c2;
     cursor: pointer;
     background-color: white;
     
}

.optionCounterButton:disabled {
     cursor: not-allowed;
     border: 1px solid lightgrey;
   
     color: gray;

}